<template>


  <div id="content" class="content">


    <el-backtop :bottom=200 :right=210></el-backtop>

    <div class="con">
      <div class="publicnews">

        <div class="imgcar">

          <el-carousel height="280px" direction="vertical">
            <el-carousel-item v-for="(img,index) in images" :key="index">

              <router-link :to="{
                path:'/bloginfo',
                query:{
                  blogid:index+1,
                }
                }">
                <el-link :underline="false">
                  <div class="carimg">
                    <el-image :src="img" fit="fill"
                              style="width: 680px; height: 280px"></el-image>

                  </div>
                </el-link>
              </router-link>
            </el-carousel-item>
          </el-carousel>
        </div>
        <div class="news">
          <el-collapse accordion>
            <el-collapse-item title="重要通知" name="1">
              <div>
                <i class="el-icon-apple"></i>
                项目已部署成功
              </div>
            </el-collapse-item>
            <el-collapse-item title="项目进展" name="2">
              <div style="margin-left: 30px;">快做完了</div>
              <div style="width: 200px;margin-top: 15px;margin-left: 40px;">
                <el-progress :text-inside="true" :stroke-width="20" :percentage="50" status="warning"></el-progress>
              </div>
            </el-collapse-item>
            <el-collapse-item title="调节网页背景颜色" name="3">
              <div>

                <el-color-picker v-model="bgcolor" @change="trunBgcolor(bgcolor)"></el-color-picker>
              </div>
            </el-collapse-item>

            <el-collapse-item title="关于作者" name="4">
              <div>
                <el-link type="info" href="https://baike.baidu.com/item/%E5%90%B4%E5%BD%A6%E7%A5%96/182990?fr=aladdin">
                  作者信息
                </el-link>
              </div>
            </el-collapse-item>
          </el-collapse>
        </div>
      </div>
    </div>
    <el-divider></el-divider>
    <div class="allBlog">

      <div class="artic" v-for="(list,index) in blogs" :key="index">
        <!--<div class="content-img">
          <a href="">
            <el-image :src="src" style="width: 176px; height: 176px;border-radius: 4px;"></el-image>
          </a>
        </div>-->
        <div class="content-tit">
          <router-link :to="{
          path:'/bloginfo',
          query:{
            blogid:list.id,
          }
          }">
            <el-link :underline="false">
              <span>{{list.title}}</span>
            </el-link>


          </router-link>

        </div>
        <div class="blog-content">
          <router-link :to="{
          path:'/bloginfo',
          query:{
            blogid:list.id,
          }
          }">
            <p>
              <el-link :underline="false">{{list.content.substr(0,300)}}<span v-if="list.content.length>300">......
            </span></el-link>
            </p>
          </router-link>
        </div>
        <div class="blog-owner">
          <span><i class="el-icon-user"></i>作者： {{list.owner}}   &nbsp; &nbsp;&nbsp;<i class="el-icon-circle-check"></i>创建时间：{{list.cdate}}</span>
          <span></span>
        </div>
        <el-divider></el-divider>
      </div>

      <div class="side-toolbar" @click="toRelease">

        <el-tooltip content="发布文章" placement="bottom" effect="light">
          <el-button icon="el-icon-edit" circle></el-button>
        </el-tooltip>

      </div>
    </div>


    <div class="number">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        background
        layout="prev, pager, next"
        :page-size="pageSize"
        :total="rows">
      </el-pagination>
    </div>


  </div>


</template>


<script>

  export default {
    name: "Content",

    data() {


      return {
        bgcolor: '#fff',
        images: (function () {
          // 导入图片模块
          let fileArr = [];
          for (let i = 0; i < 3; i++) {
            fileArr[i] = 'http://localhost:8081/img/' + (i + 1) + '.jpg';
          }
          return fileArr;
        })(),
        blogs: [],
        pageTotal: 1,
        pageSize: 6,
        rows: 1,
        currentPage: 1
      }
    },


    created() {


      this.showTable(this.currentPage, this.pageSize);


    },

    methods: {
      handleSizeChange: function (size) {
        this.pageSize = size;
        //console.log(this.pageSize);  //每页下拉显示数据
        this.showTable(this.currentPage, this.pageSize);
      },
      handleCurrentChange: function (currentPage) {
        this.currentPage = currentPage;
        //console.log(this.currentPage);  //点击第几页
        this.showTable(this.currentPage, this.pageSize);
        window.scroll(0, 390);

      },
      showTable(currentPage, pageSize) {

        this.$axios({
          method: "post",
          url: "/home/page",
          params: {
            "currentPage": currentPage,
            "pageSize": pageSize
          },
        }).then(response => {
          this.blogs = response.data.blogs;
          this.rows = response.data.rows;
        }).catch(error => {
          console.log(error)
        })


      },

      toRelease() {

        this.$router.push('release')
        /*this.$axios({
          method: "post",
          url: "/intercept/torelease",
          dataType: 'JSONP'
        }).then(result => {
          if (result.data) {
            this.$router.replace({
              name: "release",

            });
          } else {
            this.$router.replace({
              name: "loginvue",

            });
          }
        }).catch(error => {
          console.log(error)
        })*/
      },

      trunBgcolor(color) {

        document.querySelector('body').setAttribute('style', 'background-color:' + color)

      }


    }


  }

</script>

<style scoped>

  .con .imgcar {
    width: 650px;
    display: inline-block;

  }

  .con .carimg {
    width: 650px;
    height: 200px;
    overflow: visible;
  }





  .con .news {
    width: 400px;
    display: inline-block;
    float: right;

  }

  .artic {
    overflow: hidden;
    padding: 12px;
    margin: 0 0 10px 0;


  }


  .artic .content-tit {
    width: auto;
    margin-top: 15px;
    margin-left: 25px;
  }


  .artic .content-tit span {
    font-family: "Microsoft YaHei";
    color: #303133;
    font-size: 20px;
    letter-spacing: 1px;
    font-weight: 600;
  }

  .artic .content-tit :hover {
    color: #df7e65;;


  }

  .number {
    float: right;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  .side-toolbar {
    position: fixed;
    width: 50px;
    right: 200px;
    bottom: 140px;
  }

  .artic .blog-content {
    margin: 10px auto;
    width: 1200px;
  }

  .artic .blog-content p {
    font-family: "Microsoft YaHei";
    display: inline;
    text-indent: 2em;
    font-size: 16px;
    line-height: 26px;
  }

  .artic .blog-owner {
    height: min-content;
    width: 400px;
    position: relative;
    bottom: 0px;
    right: -67%;
    text-align: right;
  }

  .artic .blog-owner span {
    color: #909399;
    font-size: 13px;
    font-style: oblique;
    white-space: nowrap;
  }
</style>



