<template>
  <div class="bloginfo">
    <el-page-header @back="goBack" content="博客详情页面">
    </el-page-header>

    <div class="blogtitle">
      <p>{{blogtitle}}</p>
    </div>
    <div class="blogowner">
      <p>{{blogowner}} &nbsp;&nbsp;发布于&nbsp;&nbsp;{{blogdate}}</p>
    </div>
    <el-divider></el-divider>
    <div class="blogcontent">
      <pre>{{blogcontent}}</pre>
    </div>

  </div>
</template>

<script>
  export default {
    name: "BlogInfo",

    data() {
      return {
        blogtitle: '',
        blogcontent: '',
        blogdate: '',
        blogowner: ''

      }
    },

    created() {
      this.getinfo();

    },
    methods: {
      goBack(){
        this.$router.push({name:'home'});
      },
      getinfo() {
        var blogid = this.$route.query.blogid;

        this.$axios({
          methods: "post",
          url: "/blog/getblogbyid",
          params: {
            "id": blogid
          }
        }).then(result => {
          this.blogtitle = result.data.blog.title;
          this.blogcontent = result.data.blog.content;
          this.blogdate = result.data.blog.cdate;
          this.blogowner = result.data.blog.owner;

        }).catch(error => {
          console.log(error);
        })
      }
    }
  }
</script>

<style scoped>
  .bloginfo {
    height:auto!important;
    min-height:660px;
    background: #ffffff;
    border-radius: 4px;
    width: 100%;
    height: 660px;
    padding: 20px;
  }

  .bloginfo .blogtitle{
    margin-top: 20px;

  }
  .bloginfo .blogtitle p{
    text-align: center;
    font-size: 18px;
    color: #303133;
  }
  .bloginfo .blogowner{
    margin-bottom: -20px;
  }
  .bloginfo .blogowner p{
    color: #909399;
    text-align: right;
    font-size: 13px;
    font-style: oblique;
    white-space:nowrap;
  }
  .bloginfo .blogcontent p{
    padding: 30px;
    text-indent: 2em;
    line-height:1.7;
  }
</style>






