<template>
  <div class="content">
    <el-form ref="form" :model="form" label-width="80px" class="release-from">
      <el-form-item label="标题">
        <el-input v-model="form.title" maxlength="55" placeholder="请输入标题"></el-input>
      </el-form-item>
      <el-form-item label="文章类型">
        <el-cascader v-model="form.blogtype" placeholder="请选择文章类型" :options="options"
                     :props="props"
                     :collapse-tags="true"
                     :show-all-levels="false"></el-cascader>
      </el-form-item>

      <el-form-item label="内容">
        <el-input type="textarea" v-model="form.desc" rows="16"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">立即发布</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: "Release",
    data() {
      return {
        form: {
          title: '',
          region: '',
          desc: '',
          blogtype: ''
        },
        options: [
          {
            label: 'Spring',
            children: [{
              value: 'spring',
              label: 'Spring框架',
            },
              {
                value: 'springmvc',
                label: 'SpringMvc',
              }],
          },
          {
            value: 'mybatis',
            label: 'Mybatis',
          },
          {
            value: 'other',
            label: '其他',
            children: [{
              value: 'test',
              label: '测试',
            },
              {
                value: 'note',
                label: '笔记',
              }],
          },
        ],
        props:{
          expandTrigger: 'hover',
        }
      }
    },
    methods: {
      onSubmit() {
        let param = new URLSearchParams();
        param.append("title", this.form.title)
        param.append("content", this.form.desc)
        param.append("blogtype", this.form.blogtype[this.form.blogtype.length-1])
        param.append("owner", sessionStorage.getItem("USER_SESSION"))
        this.$axios({
          method: 'post',
          url: "/blog/releasevue",
          data: param
          /*{
            title: this.form.title,
            content: this.form.desc,
            owner: sessionStorage.getItem("USER_SESSION")
          }*/,
        }).then(response => {

          if (response.data.res) {
            this.$router.replace({name: "home"});
            this.$message({
              showClose: true,
              message: '发布成功',
              type: 'success',
              center: true
            });
          } else {
            this.$message({
              showClose: true,
              message: '发布失败',
              type: "error",
              center: true
            });
          }
        }).catch(erro => {
          console.log(erro)
        })
      }
    }
  }
</script>

<style scoped>
  .content {
    padding: 50px;
    width: 100%;
    height: 600px;
    background: #fff;
  }

  .content .release-from {
    margin: 50px 50px;

  }

</style>



