<template>
  <div class="myblog">
    <el-page-header @back="goBack" content="我的所有博客"></el-page-header>
    <div class="empty" v-if="isEmpty">
      <h2>你还没有发布过内容哦！</h2>
    </div>
    <div class="blog-content" v-else>
      <div class="artic" v-for="blog in blogs" :key="blog">
        <div class="blogtitle">
          <router-link :to="{
            path:'/bloginfo',
            query:{
            blogid:blog.id,
          }
          }">
            <el-link :underline="false"><h2>{{blog.title}}</h2></el-link>

          </router-link>

        </div>
        <div class="blogcontent">
          <p>{{blog.content.substr(0,50)}}<span v-if="blog.content.length>50">......</span></p>
        </div>

        <div class="editicon">
          <el-button-group>
            <el-button icon="el-icon-edit" @click="editblog(blog)"></el-button>
            <el-button icon="el-icon-delete" @click="deleteblog(blog.id)"></el-button>
          </el-button-group>
        </div>

      </div>

      <div class="pagination">
        <el-pagination
          hide-on-single-page="true"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          background
          layout="prev, pager, next"
          :page-size="pageSize"
          :total="rows">
        </el-pagination>
      </div>
    </div>

    <el-dialog title="编辑博客" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="博客标题" :label-width="formLabelWidth">
          <el-input v-model="form.title" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="文章类型" :label-width="formLabelWidth">
          <el-cascader v-model="form.blogtype" placeholder="请选择文章类型" :options="options"
                       :props="props"
                       :collapse-tags="true"
                       :show-all-levels="false"></el-cascader>
        </el-form-item>
        <el-form-item label="博客内容" :label-width="formLabelWidth">
          <el-input type="textarea" v-model="form.desc" rows="16"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateblog()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "MyBlog",
    inject: ['reload'],
    data() {
      return {
        upblogid: '',
        form: {
          title: '',
          region: '',
          desc: '',
          blogtype: ''
        },
        options: [
          {
            label: 'Spring',
            children: [{
              value: 'spring',
              label: 'Spring框架',
            },
              {
                value: 'springmvc',
                label: 'SpringMvc',
              }],
          },
          {
            value: 'mybatis',
            label: 'Mybatis',
          },
          {
            value: 'other',
            label: '其他',
            children: [{
              value: 'test',
              label: '测试',
            },
              {
                value: 'note',
                label: '笔记',
              }],
          },
        ],
        props: {
          expandTrigger: 'hover',
        },
        blogs: '',
        isEmpty: true,
        dialogFormVisible: false,
        formLabelWidth: '120px',
        pageSize: 4,
        rows: 1,
        currentPage: 1
      }
    },
    created() {

      this.getblogbyname(sessionStorage.getItem("USER_SESSION"), this.currentPage, this.pageSize);
    },
    methods: {
      goBack() {
        this.$router.push({name: 'home'});
      },
      handleSizeChange: function (size) {
        this.pageSize = size;
        //console.log(this.pageSize);  //每页下拉显示数据
        this.getblogbyname(sessionStorage.getItem("USER_SESSION"), this.currentPage, this.pageSize);
      },

      handleCurrentChange: function (currentPage) {
        this.currentPage = currentPage;
        //console.log(this.currentPage);  //点击第几页
        this.getblogbyname(sessionStorage.getItem("USER_SESSION"), this.currentPage, this.pageSize);

      },

      getblogbyname(name, currentPage, pageSize) {

        this.$axios({
          methods: "post",
          url: "/blog/getblogbyname",
          params: {
            "username": name,
            "currentPage": currentPage,
            "pageSize": pageSize
          }
        }).then(result => {
          if (result.data.res) {
            this.isEmpty = false;
            this.blogs = result.data.blogs;
            this.rows = result.data.rows;
          }
        }).catch(error => {
          console.log(error)
        })

      },

      editblog(blog) {
        this.dialogFormVisible = true;
        this.form.title = blog.title;
        this.form.desc = blog.content;
        this.form.blogtype = blog.blogtype;
        this.upblogid = blog.id;

      },

      deleteblog(blogid) {
        this.$confirm('此操作将永久删除该条博客, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$axios({
            methods: "post",
            url: "/blog/deleteblogbyid",
            params: {
              "id": blogid
            }
          }).then(result => {
            if (result.data.res) {
              this.$message({
                type: 'success',
                message: '删除成功!',
                center: true
              });
              this.reload();
            } else {
              this.$message({
                type: 'error',
                message: '删除失败!',
                center: true
              });
            }
          }).catch(error => {
            console.log(error)
          })

        }).catch(() => {

        });
      },

      updateblog() {
        let param = new URLSearchParams();
        param.append("id", this.upblogid)
        param.append("content", this.form.desc)
        param.append("title", this.form.title)
        param.append("blogtype", this.form.blogtype[this.form.blogtype.length-1])
        this.$axios({
          method: 'post',
          url: "/blog/updateblogbyid",
          data: param
          /*{
            "id": this.upblogid,
            "content": this.form.desc,
            "title": this.form.title
          }*/
        }).then(result => {
          if (result.data.res) {
            this.dialogFormVisible = false;
            this.$message({
              type: 'success',
              message: '修改成功!',
              center: true
            });
            this.reload();

          } else {
            this.$message({
              type: 'error',
              message: '修改失败!',
              center: true
            });
          }
        }).catch(error => {
          console.log(error);
        })
      }
    }
  }
</script>

<style scoped>
  .myblog {
    background: #ffffff;
    border-radius: 4px;
    width: 100%;
    height: 669px;
    padding: 20px;
  }

  .myblog .empty {
    position: relative;
    width: 400px;
    height: 100px;
    top: 50%;
    left: 50%;
    margin-top: -50px;
    margin-left: -200px;
  }

  .myblog .empty h2 {
    color: #909399;
    font-style: oblique;
  }

  .myblog .blog-content {
    margin-top: 20px;
  }

  .myblog .blog-content .artic {
    padding: 20px;
    margin-bottom: 20px;
    width: 95%;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    height: 70px;
    border-radius: 8px;
  }

  .myblog .blog-content .artic:hover {
    background: #F2F6FC;
  }

  .myblog .blog-content .artic .blogtitle {
    display: inline-block;
    margin: 4px 20px;
    width: 400px;
  }

  .myblog .blog-content .artic .blogtitle h2 {
    margin: 0;
    font-family: 宋体;
    color: #303133;
    font-size: 20px;

    font-weight: 600;
  }

  .myblog .blog-content .artic .blogcontent {
    display: inline-block;
    margin-left: 20px;
    width: 800px;
  }

  .myblog .blog-content .artic .blogcontent p {
    color: #909399;
    text-indent: 2em;
    font-size: 14px;
    font-style: oblique;
    font-weight: 500;
  }

  .myblog .blog-content .artic .editicon {
    position: relative;
    right: -91%;
    bottom: 100%;
    width: 200px;

  }

  .myblog .blog-content .pagination {
    text-align: right;
    width: 400px;
    position: absolute;
    bottom: 9%;
    right: 16%;
  }
</style>



